import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PartnerListComponent } from './partner-list.component';
import { AddPartnerComponent } from './add-partner/add-partner.component';
import { EditPartnerComponent } from './edit-partner/edit-partner.component';
import { RouterModule, Routes } from '@angular/router';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { MaterialModule } from 'src/app/dealer/material.module';

const routes: Routes = [
  {
    path: '',
    component: PartnerListComponent,
  },
];

@NgModule({
  declarations: [PartnerListComponent, AddPartnerComponent, EditPartnerComponent],
  imports: [
    CommonModule,
    ReactiveFormsModule,
    MaterialModule,
    FormsModule,
    RouterModule.forChild(routes),
  ]
})
export class PartnerListModule { }
