import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
// import { FirebaseAuth, QueryVhAPP } from 'private-angular-vhframeworks';
import { AddPartnerComponent } from './add-partner/add-partner.component';
import { EditPartnerComponent } from './edit-partner/edit-partner.component';

@Component({
  selector: 'app-partner-list',
  templateUrl: './partner-list.component.html',
  styleUrls: ['./partner-list.component.scss'],
})
export class PartnerListComponent implements OnInit {
  @ViewChild(MatPaginator) paginator: MatPaginator;
  /// panigatior ///
  loading: boolean = false;
  pageIndex: number = 0;
  pageSize: number = 10;
  lowValue: number = 0;
  highValue: number = 10;
  pageSizeOption: any = [10, 25, 50, 100];
  startTime: any = new Date();
  endTime: any = new Date();
  list_partner: any = [];
  constructor(
    // private queryVhAPP: QueryVhAPP,
    public dialog: MatDialog,
    // public firebaseAuth: FirebaseAuth
  ) {}

  ngOnInit(): void {
    this.loading = true;
    this.getPartners();
  }

  getPartners() {
    // this.firebaseAuth.getPartners().then(
    //   (data) => {
    //     if (data) {
    //       this.loading = false;
    //       this.list_partner = data;
    //     }
    //   },
    //   (err) => {
    //     this.loading = false;
    //     console.log(err);
    //   }
    // );
  }

  openAddEMployee() {
    const dialogRef = this.dialog.open(AddPartnerComponent, {
      width: '70%',
      maxWidth: '100%',
      disableClose: true,
    });

    dialogRef.afterClosed().subscribe((result) => {
     if(result) this.getPartners();
    });
  }
  opeEditEmployee(item) {
    const dialogRef = this.dialog.open(EditPartnerComponent, {
      width: '85%',
      maxWidth: '100%',
      data: item,
      disableClose: true,
    });

    dialogRef.afterClosed().subscribe((result) => {
      if(result) this.getPartners();
    });
  }

  // slice panigator
  getPaginatorData(event: PageEvent): PageEvent {
    this.lowValue = event.pageIndex * event.pageSize;
    this.highValue = this.lowValue + event.pageSize;
    return event;
  }
}
